export default class extends React.Component {
    static async getInitialProps() {
        let { data: products } = await requestService({ path: 'products'})
    
        return { products }
    }
    
    render() {
        const { products } = this.props

        return (
            <div className="products">
                {
                    products.map((product) => (
                    <div>
                        {product.name}
                    </div>
                )) }
            </div>
        )
    }
}